.header {
  /* max-width: 1200px ; */
  margin: 0 auto;
 }
 
 .header a {
   color: #fff;
   text-decoration: none;
 }

 .nav-wrapper {
  display: flex;
  background-color: white;
  color: black;
  margin: 0 3rem;
  font-family: 'Roboto', sans-serif;
  width: 100vw;
}

.nav-logo {
  padding: 1rem;
  align-self: center;
  grid-area: logo;
}

.nav-logo img {
  width: 200px;
}

.nav-list ul {
  list-style: none;
  display: flex;
 
 }
 .nav-list ul li {
  padding: 1rem;
  font-size: 1rem;
 }
 .nav-list ul li a {
  padding: 1rem;
  font-size: 0.8rem;
  color: black;
 }

 /* SEARCH BAR */
  .searchbar-wrapper {
    display: flex;
    flex: 1;
    justify-content: end;
    position: relative;
    grid-area: searchbar;
  }

  .ais-SearchBox-wrapper{
    align-self: center;
    position: absolute;
    width: 60%;
    right: 5rem;
  }

  .ais-SearchBox-input{
    background-color:rgb(246,245,243);
    border: none;
    padding: 0.6rem;
    padding-left: 3rem;
    border-radius: 5px;
  }
  .ais-SearchBox-input::placeholder{
    color: grey;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    font-style: italic;
    letter-spacing: .1rem;
    font-size: 0.8rem;
  }
  .ais-SearchBox-input:focus{
   outline: none;
  }

  /* BURGER MENU */

  .burger {
    display: none;
    align-self: center;
    grid-area: burger;
}

.burger div {
    height: 3px;
    width: 35px;
    margin: 5px;
    border-radius: 15px;
    background-color: black;
    transition: all 0.4s ease;
}

.nav-active {
 transform: translateX(0) !important;
 transition: transform 0.5s ease-in-out;
}

.displayFlex{
  display: flex !important;
}

.toggle .line1{
    transform: rotate(-45deg) translate(-5px, 6px);
}
.toggle .line2{
    opacity: 0;
}
.toggle .line3{
    transform: rotate(45deg) translate(-5px, -6px);
}



@media screen and (max-width: 1024px){

  .searchbar-wrapper{
   width: 100%;
   justify-self: center;
    }

  .nav-wrapper{
      display: grid;
      grid-template-columns: 25% 1fr 25%;
      justify-content: center;
      grid-template-areas: "logo searchbar burger";
      margin: 0 1rem;
      justify-items: center;
  }
  .burger {
      display: block;
      cursor: pointer;
  }
  .nav-list ul {
      position: absolute;
      right: 0;
      top: 3.6rem;
      height: 100vh;
      z-index: 1000;
      background-color: black;
      color: white;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 35%;
      transform: translateX(100%);
      transition: transform 0.5s ease-in-out;
      list-style: none;
  }
  .nav-list ul li a{
      opacity: 0;
      color: white; 
  }
}

 @media screen and (max-width: 768px){
  .burger {
      display: block;
  }
 }
 @media screen and (max-width: 560px){
  body {
    overflow-x: hidden !important;
  }

  .header{
    width: 100vw;
    padding-bottom: 1rem;   
  }

  .nav-wrapper{
    display: grid;
    grid-template-columns: 25% 1fr 25%;
    justify-content: center;
    grid-template-areas: ". logo burger"
                          "searchbar searchbar searchbar";
    margin: 0 auto;
    justify-items: center;
}

  .searchbar-wrapper{
    width: 100%;
    margin: 1rem auto;
    justify-self: center;
  }

  .btn-filter{
    font-size: 0.8rem;
  }

   .burger div {
    height: 2px;
    width: 25px;
  }

  .ais-Hits-item {
    height: 450px;
    padding: 1.5rem;
    margin: auto 1rem;
    width: 250px;
  }

  .nav-list ul {
  top: 5.6rem;
  width: 60%;
  }

  .ais-SearchBox-input::placeholder {
    font-size: 0.6rem;
  }
}  

@media screen and (max-width: 361px){
body {
  overflow-x: hidden !important;
}
}

 @keyframes navlinksFade {
     from {
         display: none;
         opacity: 0;
     }
     to {
         display: flex;
         opacity: 1;           
     }
 }
 @keyframes navLinksMove {
     from {
         transform: translateX(100px);
     }
     to {
          transform: translateX(0px);
     }
 }